import { forwardRef, Component, Input, ContentChild, SkipSelf, Host, Optional  } from '@angular/core';
import { GroupHeaderTemplateDirective } from './group-header-template.directive';
import { GroupFooterTemplateDirective } from './group-footer-template.directive';
import { FooterTemplateDirective } from './footer-template.directive';
import { ColumnBase } from './column-base';
import { CellOptions } from '../ooxml/cell-options.interface';

/**
 * Represents the columns of the Kendo UI ExcelExport component for Angular.
 */
@Component({
    providers: [
        {
            provide: ColumnBase,
            useExisting: forwardRef(() => ColumnComponent)// tslint:disable-line:no-forward-ref
        }
    ],
    selector: 'kendo-excelexport-column',
    template: ``
})
export class ColumnComponent extends ColumnBase {
    /**
     * The field to which the column is bound.
     */
    @Input()
    public field: string;

    /**
     * The options of the column data cells.
     */
    @Input()
    public cellOptions: CellOptions;

    /**
     * The options of the column group header cells.
     */
    @Input()
    public groupHeaderCellOptions: CellOptions;

    /**
     * The options of the column group footer cells.
     */
    @Input()
    public groupFooterCellOptions: CellOptions;

    /**
     * The options of the column footer cell.
     */
    @Input()
    public footerCellOptions: CellOptions;

    /**
     * @hidden
     */
    @ContentChild(GroupHeaderTemplateDirective)
    public groupHeaderTemplate: GroupHeaderTemplateDirective;

    /**
     * @hidden
     */
    @ContentChild(GroupFooterTemplateDirective)
    public groupFooterTemplate: GroupFooterTemplateDirective;

    /**
     * @hidden
     */
    @ContentChild(FooterTemplateDirective)
    public footerTemplate: FooterTemplateDirective;

    constructor(@SkipSelf() @Host() @Optional() parent?: ColumnBase) {
        super(parent);
    }
}



